import alasql from 'alasql';
import { DefaultButton, SearchBox } from 'office-ui-fabric-react';
import React, { useEffect, useState } from 'react';
import atzoatl from '../../data/atzoatl';
import './Atzoatl.scss';
import TableRow from './TableRow';
// The Temple of Atzoatl

function getKeywords(rawKeyword: string): string[] {
  const spliteds = rawKeyword.split(' ');
  const filtereds = spliteds
    .filter((splited) => splited !== '')
    .filter((splited) => {
      return !/^\s+$/g.test(splited);
    });

  return filtereds;
}

function revert(
  setKeyword: React.Dispatch<React.SetStateAction<string>>,
  setCodes: React.Dispatch<React.SetStateAction<string[]>>,
) {
  setKeyword('');

  const fetchedCodes: Array<{ code: string }> = alasql('SELECT code FROM ? GROUP BY code', [atzoatl]);
  const initCodes = fetchedCodes.map((fetchedCode) => fetchedCode.code);

  setCodes(initCodes);
}

function searching(keywords: string[], setCodes: React.Dispatch<React.SetStateAction<string[]>>) {
  // 검색으로 해볼려고 했는데, 정확한 값만 검색이 되고 LIKE가 동작하지 않아서 빠른 포기
  // const keywords = newValue.split(' ');
  // const a = alasql('SEARCH /name\nWHERE(ko LIKE "%세공%") FROM ?', [atzoatl]);

  const codesSet = new Set(
    keywords
      .map((keyword) => {
        const fetchedCodes: Array<{ code: string }> = alasql('SELECT code FROM ? WHERE name->ko LIKE ? GROUP BY code', [
          atzoatl,
          `%${keyword}%`,
        ]);
        return fetchedCodes.map((fetchedCode) => fetchedCode.code);
      })
      .reduce((aggregation, current) => {
        return [...aggregation, ...current];
      }, []),
  );

  setCodes(Array.from(codesSet));
}

const AtzoatlPage: React.FC = () => {
  const [codes, setCodes] = useState<string[]>([]);
  const [keyword, setKeyword] = useState<string>('');

  useEffect(() => {
    revert(setKeyword, setCodes);
  }, []);

  const onHandleSearchBoxSubmit = (newValue: any) => {
    if (typeof newValue !== 'string') {
      return;
    }

    searching(getKeywords(newValue), setCodes);
  };

  const [tmr, setTmr] = useState<NodeJS.Timeout | null>(null);

  const onHandleSearchBoxChange = (_event?: React.ChangeEvent<HTMLInputElement>, newValue?: string) => {
    if (typeof newValue !== 'string') {
      return;
    }

    setKeyword(newValue || '');

    const interval = 1000;

    if (tmr !== null) {
      clearTimeout(tmr);
    }

    const tm = setTimeout(() => {
      newValue === '' ? revert(setKeyword, setCodes) : searching(getKeywords(newValue), setCodes);
      setTmr(null);
    }, interval);
    setTmr(tm);
  };

  return (
    <div className="body-ctr">
      <h1 className="atzoatl-table-title">앗조아틀 사원 보상</h1>

      <div className="search-box-ctr">
        <div className="search-ctrl-ctr">
          <SearchBox
            placeholder="방 이름을 입력하세요"
            onSearch={onHandleSearchBoxSubmit}
            iconProps={{ iconName: 'Filter' }}
            underlined={true}
            value={keyword}
            // onFocus={() => console.log('onFocus called')}
            // onBlur={() => console.log('onBlur called')}
            onChange={onHandleSearchBoxChange}
          />
        </div>

        <div className="search-search-btn-ctr">
          <DefaultButton
            data-automation-id="test"
            allowDisabledFocus={true}
            text="초기화"
            onClick={() => revert(setKeyword, setCodes)}
          />
        </div>
      </div>

      <table className="atzoatl-table-ctr">
        <thead>
          <tr>
            <th>구분</th>
            <th>이름</th>
            <th>레벨</th>
            <th>추천</th>
            <th>긍정적 보상</th>
            <th>부정적 보상</th>
          </tr>
        </thead>
        <tbody>{codes.map((code, index) => TableRow({ code, order: index + 1 }))}</tbody>
      </table>
    </div>
  );
};

export default AtzoatlPage;
